export default abstract class LocalStorageAbstractClass<T>
{
    abstract key: string;

    Get(defaultValue: T): T
    {
        let output = localStorage.getItem(this.key);
        return (output) ? JSON.parse(output) : defaultValue;
    }

    CommitChanges(value: T)
    {
        localStorage.setItem(this.key, JSON.stringify(value));
    }
}